<template>
  <div class="container">
    <h1>Instructor Application</h1>
    <div class="course-title">
      <h3>Course</h3>
      <button class="btn btn-success" @click="backCourses()">
       Back
      </button>
    </div>
    <div v-if="successMessage" class="alert alert-success alert-dismissable">
      <button class="close" data-dismiss="alert" @click="successMessage = null">
        &times;
      </button>
      {{ successMessage }}
    </div>
    <div v-if="errorMessage" class="alert alert-danger alert-dismissable">
      <button class="close" data-dismiss="alert" @click="errorMessage = null">
        &times;
      </button>
      {{ errorMessage }}
    </div>
    <form>
      <fieldset class="form-group">
        <label>Description</label>
        <input
          type="text"
          id="description"
          class="form-control"
          v-model="course.description"
          v-focus
        />
      </fieldset>
      <fieldset class="form-group">
        <label>Language</label>
        <input type="text" class="form-control" v-model="course.language" />
      </fieldset>
      <div class="form-group">
        <label for="type">Format</label>
        <div class="form-check">
          <input
            class="form-check-input"
            type="radio"
            v-model="course.format"
            name="format"
            id="in-person"
            value="in-person"
            checked
          />
          <label class="form-check-label" for="inPerson">In-person</label>
        </div>
        <div class="form-check">
          <input
            class="form-check-input"
            type="radio"
            v-model="course.format"
            name="format"
            id="online"
            value="online"
          />
          <label class="form-check-label" for="online">Online</label>
        </div>
      </div>
      <fieldset class="form-group">
        <label>Price</label>
        <input type="text" class="form-control" v-model="course.price" />
      </fieldset>
    </form>
    <button class="btn btn-success" @click="validateAndSubmit()">Save</button>
    <button class="btn btn-warning" @click="backCourses()">Cancel</button>
  </div>
</template>

<script>
import CourseDataService from "../service/CourseDataService";
export default {
  name: "AddCourse",
  data() {
    return {
      course: {
        description: "",
        language: "",
        format: "",
        price: null,
      },
      successMessage: "",
      errorMessage: "",
      coursesList: this.courses,
    };
  },
  props: {
    courses: Array,
  },

  methods: {
    validateAndSubmit() {
      try {
        const newCourse = CourseDataService.validateCourse(this.course);
        this.createCourse(newCourse);
      } catch (error) {
        this.errorMessage = error.message;
      }
    },
    async createCourse(newCourse) {
      try {
        const response = await CourseDataService.createCourse(newCourse);
        this.successMessage = "Course created successfully";
        this.refreshCoursesList(response.data);
        this.clearForm();
      } catch (error) {
        this.errorMessage = "Couldn't create this course, try it again later";
      }
    },
    refreshCoursesList(newCourse) {
      const newList = [...this.courses, newCourse];
      this.$emit("courses", newList);
      localStorage.setItem("courses", JSON.stringify(newList));
    },
    clearForm() {
      this.course = {};
      setTimeout(() => {
        this.successMessage = null;
        document.getElementById("description").focus()
      }, 2000);
    },
    backCourses() {
      this.$router.push({ name: "Home" });
    },
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus();
      },
    },
  },
};
</script>

<style scoped>
.course-title {
  display: flex;
}

.course-title > button {
  margin-left: auto;
  margin-bottom: 10px;
  margin-right: 0;
}

button {
  margin-right: 10px;
}

</style>